import { init, send } from "emailjs-com";
import React, { useState } from "react";
import { Button, Input } from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import "../css/estilos_contacto.css";
import "../css/estilos_newsletter.css";
import logo from "../components/img/newsletter.png";
//import "../js/validar";

init("user_aUU0Vn7SifIuuMMkkLn9o");

export default function NewsletterPage() {
  const [toSend, setToSend] = useState({
    from_name: "",
    from_email: "",
  });

  const onSubmit = (e) => {
    e.preventDefault();

    send(
      "gmail_mensaje",
      "template_o6psm5e",
      toSend,
      "user_aUU0Vn7SifIuuMMkkLn9o"
    )
      .then((response) => {
        console.log("SUCCESS!", response.status, response.text);
      })
      .catch((err) => {
        console.log("FAILED...", err);
      });
  };

  const handleChange = (e) => {
    setToSend({ ...toSend, [e.target.name]: e.target.value });
  };

  return (
    <div>
      <div className="header-news">
        <img src={logo} alt="" className="logo laptop" />
        <div className="texto-news z-index">
          <p className="texto-home">
            Si buscas cambiar tu estilo de vida con prácticas saludables para
            cuerpo y mente
            <br></br>
            Si buscas transformarte
            <br></br>
            Si buscas vivir en paz has llegado a un lugar donde con nuestra
            experiencia de 20 años inspirados en el Ayurveda como medicina y
            camino de conciencia compartimos salud, esperanza, equilibrio y
            transformación.
          </p>
        </div>
        <div className="overly-header"></div>
      </div>
      <div className="form-news z-index">
        <h1 className="titulo_suscripcion z-index">Suscribete!</h1>
        <form onSubmit={onSubmit}>
          <Input
            id="fromName"
            type="text"
            name="from_name"
            placeholder="Tu nombre (obligatorio)"
            value={toSend.from_name}
            onChange={handleChange}
          />
          <Input
            id="email"
            type="text"
            name="from_email"
            placeholder="Tu email (obligatorio)"
            value={toSend.from_email}
            onChange={handleChange}
          />
          <Button type="submit" id="btn-contact">
            Enviar
          </Button>
        </form>
      </div>
    </div>
  );
}
