import React from "react";
import reactDOM from "react-dom";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import "./App.css";
import "../src/css/estilos_barra.css";
import "../src/css/estilos_home.css";
import "../src/css/estilos_slider.css";
import Contacto from "./components/form";
import Clases from "./components/Clases";
import Cursos from "./components/Cursos";
import UserDashboard from "./components/user";
import NewsletterPage from "./components/Newsletterpage";
import reactRedirection from "./components/Reactredirection";
import Header from "./components/Header";
import Whatsapp from "./components/whatsapp";
import Home from "./components/home";
import Login from "./components/login";

const routes = (
  <BrowserRouter>
    <div>
      <Header />
      <Whatsapp />
      <Switch>
        <Route path="/" component={Home} exact={true} />
        <Route path="/react" component={reactRedirection} />
        <Route path="/clases" component={Clases} />
        <Route path="/cursos" component={Cursos} />
        <Route path="/newslettersub" component={NewsletterPage} />
        <Route path="/contacto" component={Contacto} />
        <Route path="/userdashboard" component={UserDashboard} />
        <Route path="/login" component={Login} />
      </Switch>
    </div>
  </BrowserRouter>
);

reactDOM.render(routes, document.getElementById("root"));
