import React from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  FormGroup,
  Input,
  Label,
} from "reactstrap";
import { Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.css";

class Link_clases extends React.Component {
  state = {
    abierto: false,
  };

  abrirModal = () => {
    this.setState({ abierto: !this.state.abierto });
  };
  confirmar = () => {
    let value = document.getElementById("password").value;
    if(value > 999 && value < 10000){
      alert("te enviaremos los link a tu correo");
    }else{
      alert("parece que tu cuenta no tiene premium o el codigo no es correcto");
    }
  };


  render() {
    const modalStyles = {
      position: "absolute",
      top: "50%",
      left: "50%",
      transform: "translate(-50%, -50%)",
      width: "50%",
    };

    return (
      <>
        <div className="principal">
          <div className="secundario">
            <Button
              color="success"
              className="btn-modal"
              onClick={this.abrirModal}
            >
              Suscribete a nuestra newsletter
            </Button>
          </div>
        </div>

        <Modal isOpen={this.state.abierto} style={modalStyles}>
          <ModalHeader>
            <p>Iniciar Sesión</p>
          </ModalHeader>
          <ModalBody>
            <FormGroup>
              <p>
                <span className="text-danger">* </span>
                Revisa en el{" "}
                <Link to="/userdashboard">
                  <span>centro de usuario</span>
                </Link>{" "}
                para obtener tu codigo vip
              </p>
            </FormGroup>
            <FormGroup>
              <Label for="password">Codigo vip</Label>
              <Input type="text" id="password" />
            </FormGroup>
          </ModalBody>

          <ModalFooter>
            <Button color="primary" onClick={this.confirmar}>Confirmar</Button>
            <Button color="secondary" onClick={this.abrirModal}>
              Cerrar
            </Button>
          </ModalFooter>
        </Modal>
      </>
    );
  }
}

export default Link_clases;
