import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import random from "random";

class ObtenerCodigo extends React.Component {
  state = {
    abierto: false,
  };

  abrirModal = () => {
    this.setState({ abierto: !this.state.abierto });
  };

  render() {
    const normal = random.int(1000, 10000);
    let os = navigator.appVersion.indexOf;
    let UserCode = {
      code: normal,
      sistema: os,
    };

    const modalStyles = {
      position: "absolute",
      top: "50%",
      left: "50%",
      transform: "translate(-50%, -50%)",
      width: "50%",
    };
    return (
      <>
        <div className="principal">
          <div className="secundario">
            <Button
              color="success"
              className="btn-modal btn-code"
              onClick={this.abrirModal}
            >
              Obtener codigo vip
            </Button>
          </div>
        </div>

        <Modal isOpen={this.state.abierto} style={modalStyles}>
          <ModalHeader>
            <p>Codigo vip</p>
          </ModalHeader>
          <ModalBody>
            <p className="p-code">{UserCode.code}</p>
          </ModalBody>

          <ModalFooter>
            <Button color="primary" onClick={this.abrirModal}>
              Cerrar
            </Button>
          </ModalFooter>
        </Modal>
      </>
    );
  }
}

export default ObtenerCodigo;
